
.select2-container--default {
    .select2-results__option[aria-selected=true] {
        background: none; }
    .select2-results__option--highlighted[aria-selected] {
        background: none;
        color: inherit;
        text-decoration: underline; }
    .select2-selection--single {
        background-color: #efece6;
        border: none;
        height: 33px;
        .select2-selection__rendered {
            padding-left: 34px;
            line-height: 33px;
            color: $brown_orange; } } }

.all-companies {
    padding-top: 36px;
    &__tile {
        margin-bottom: 60px; }
    &__list {
        margin-bottom: 60px;
        .companies-item {
            margin-bottom: 16px;
            &:last-child {
                margin-bottom: 60px; }
            &__wrap {
                flex-direction: row; }
            &__name {
                margin-bottom: 2px; }
            &__type {
                margin-bottom: 22px; }
            &__descr {
                margin-bottom: 18px; }
            p {
                line-height: 1.6;
                margin-bottom: 25px; }
            .sf-thumb {
                max-width: 270px;
                margin: 0 25px 0 0; } } }

    &__head {
        margin-bottom: 43px; }
    &__top {
        .shop-sort-wrap {
            padding: 4px 17px 4px 28px;
            background-color: $white_brown_bg; }
        .ps-options {
            display: flex;
            align-items: center;
            margin-bottom: 0;
            span {
                width: auto;
                margin-right: 22px;
                font-size: 13px;
                color: $brown_orange; } }
        .ps-select {
            background-color: #fff;
            width: 90px;
            height: 31px;
            select {
                color: $brown_orange;
                font-size: 13px;
                text-align: center;
                padding: 0 41px;
                height: 31px; }
            i {
                line-height: 31px; } }
        .shop-sort {
            font-family: Flaticon; } }
    &__title {
        line-height: 0.9;
        margin-bottom: 7px; }
    .select {
        margin-bottom: 30px;
        h5 {
            margin-bottom: 12px;
            border: none; } }
    .side-form {
        button {
            display: block; } } }
