.companies-item {
    display: block;
    padding: 7px 10px;
    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: flex-start; }
    &__text {
        display: flex;
        flex-direction: column;
        align-items: flex-start; }
    &__name {
        margin-bottom: 4px;
        @include sm {
            order: 2; } }
    &__type {
        font-size: 14px;
        color: #b4aa96;
        margin-bottom: 20px;
        @include sm {
            order: 1; } }
    &__descr {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-bottom: 16px;
        @include sm {
            flex-direction: column;
            align-items: flex-start;
            order: 3; } }
    &__region {
        color: #b4aa96;
        font-size: 12px; }
    &:hover {
        box-shadow: 3px 3px 16px 0 rgba(0,0,0,.2);
        .product-reviews__details {
            visibility: visible; } }
    .btn {
        order: 4; }
    .sf-thumb {
        height: 234px;
        line-height: 234px;
        text-align: center;
        border: 1px solid #e4e4e4;
        width: 100%;
        margin-bottom: 28px;
        &:after {
            display: none; }
        img {
            max-height: 234px;
            max-width: 100%;
            width: auto;
            vertical-align: middle; } }
    .flaticon-favorite {
        font-family: Flaticon; }
    .product-reviews {
        display: flex;
        align-items: center;
        margin-right: 5px;
        &__details {
            visibility: hidden;
            transition: all .4s;
            white-space: nowrap;
            span {
                font-size: 12px;
                color: #c3beb6; }
            .icon-users {
                fill: #c3beb6;
                font-size: 10px; } }
        .pr-ratings {
            white-space: nowrap;
            font-size: 0;
            margin-right: 5px;
            i {
                font-size: 9px;
                color: $brown_orange;
                top: 0;
                margin-right: 1px; } } } }
